import '../styles/index.scss';
if (process.env.NODE_ENV === 'development') {
	require('../index.html');
}

window.addEventListener('DOMContentLoaded', function () {
	'use strict';


	const tabs = document.querySelectorAll('._3 .tab');

	tabs.forEach(tab => {
		tab.addEventListener('click', e => {
			e.preventDefault();

			tabs.forEach(tab => {
				tab.classList.remove('active');
			});

			this.document.querySelectorAll('._3 .tab_contents').forEach(cnt => {
				cnt.classList.remove('active');
			});

			tab.classList.add('active');
			document.querySelector('.' + tab.dataset.label).classList.add('active');
		})
	});

	const items = document.querySelectorAll('._6 .items_box__item');

	console.log(items);

	items.forEach(element => {
		element.addEventListener('click', e => {
			e.preventDefault();

			console.log(element);

			if(element.classList.contains('active')){
				element.classList.remove('active');
			} else {
				element.classList.add('active');
			}
		})
	});

});
